import axios, { AxiosRequestConfig } from "axios";
import { TWOONONE_API } from "../Global";
import { twoonone } from "@/protos/proto";

axios.defaults.withCredentials = true

const prefix = '/v1'

const GetRooms = () => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms`,
    method: "get"
}

const GetRoom = (req: twoonone.GetRoomRequest) => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}`,
    method: "get"
}

const GetDaliyCoin = () => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/get_daliy_coin`,
    method: "get",
}

const CreateRoom = () => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms`,
    method: 'post',
}

const JoinRoom = (req: twoonone.JoinRoomRequest) => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}/join`,
    method: 'get',
}

const ExitRoom = (req: twoonone.ExitRoomRequest) => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}/exit`,
    method: 'get',
}

const RobLandowner = (req: twoonone.RobLandownerRequest) => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}/play/robLandowner`,
    method: 'get',
}

const NoRobLandowner = (req: twoonone.NoRobLandownerRequest) => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}/play/noRobLandowner`,
    method: "get",
}

const SendCard = (req: twoonone.SendCardRequest) => {
    const j = JSON.stringify({
        'sendcards': req.sendcards
    })
    console.log(j)
    return <AxiosRequestConfig>{
        url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}/play/sendCard`,
        method: "post",
        data: j,
        headers: {
            "Content-Type": 'application/json'
        }
    }
}

const NoSendCard = (req: twoonone.NoSendCardRequest) => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}/play/noSendCard`,
    method: "get",
}

const StartRoom = (req: twoonone.StartRoomRequest) => <AxiosRequestConfig>{
    url: `${TWOONONE_API}${prefix}/rooms/${req.room_hash}/start`,
    method: "get",
}

export {
    GetRoom,
    GetRooms,
    GetDaliyCoin,
    CreateRoom,
    JoinRoom,
    ExitRoom,
    RobLandowner,
    NoRobLandowner,
    SendCard,
    NoSendCard,
    StartRoom
}