import { twoonone } from "@/protos/proto";

export class BasicResponse {
    code:twoonone.api.Error = twoonone.api.Error.ERROR_UNKNOWN
    msg:string = "API"
    data?:any
}

export class TwoononeError extends Error {
    code: twoonone.api.Error
    msg: string

    constructor(code:twoonone.api.Error, msg:string) {
        super(`error code: ${code}, msg: ${msg}`)
        this.code = code
        this.msg = msg
    }
}