import { twoonone } from "@/protos/proto";
import * as api from "./api";
import axios, { HttpStatusCode } from "axios";
import { BasicResponse, TwoononeError } from "./define";
import { TWOONONE_API_HOST } from "../Global";
import { reactive, ref, watch } from "vue";

axios.defaults.withCredentials = true

async function GetRooms():Promise<twoonone.GetRoomsResponse|twoonone.api.Error> {
    const req_body = api.GetRooms()
    try {
        const resp = await axios.request(req_body)
        const j = resp.data as BasicResponse
        if (j.code !== 0) {
            throw new TwoononeError(j.code, j.msg)
        }
        return new twoonone.GetRoomsResponse(j.data)
    } catch (e) {
        const code = errorHandle(e)
        if (code !== undefined) {
            return code
        }
    }
    return twoonone.api.Error.ERROR_UNKNOWN
}

