async function RoomEvent(): Promise<AsyncGenerator<twoonone.RoomEventResponse, void, unknown>|twoonone.api.Error> {
    let ws:WebSocket
    const state = ref<{err?: Error}>({})
    const msg = reactive<MessageEvent[]>(new Array<MessageEvent>)
    const closed = ref<CloseEvent>(new CloseEvent(''))
    const wait = new Promise<void>((done) => {
        watch(state, () => done(), {once: true})
    })
    // 
    new Promise<void>((done, reject) => {
        watch(state, 
        v => {
            if (v.err !== undefined) {
                reject(v.err)
            } else {
                done()
            }
        },
        {once: true})
    }).catch(e => {
        throw e
    })
    new Promise(() => {
        ws = new WebSocket(`wss://${TWOONONE_API_HOST}/ws`)
        ws.onmessage = async me => {
            console.log('onmessage trigger')
            msg.push(me)
        }
        ws.onclose = e => closed.value = e
        ws.onopen = () => state.value = {}
        ws.onerror = () => state.value = {err: Error('websocket')}
    })
    // 
    await wait
    // 
    try {
        const code = await new Promise<twoonone.api.Error>((done,reject) => {
            // 0.5
            const timer_id = setTimeout(() => reject(), 500)
            watch(msg,
                mes => {
                    clearTimeout(timer_id)
                    const me = mes.at(mes.length-1)
                    if (me === undefined) {
                        console.error(undefined)
                        reject(undefined)
                        return
                    }
                    const j = JSON.parse(me.data)
                    if ((j as BasicResponse).code !== 0) {
                        done(j.code)
                    } else {
                        reject()
                    }
                },
                {once: true}
            )
        })
        return code
    } catch (e) {
        if (e instanceof Error) throw e
    }
    const sync = (async function* () {
        const handle = (me: MessageEvent) => {
            const j = JSON.parse(me.data)
            if (j.code !== 0) return
            return j.data.Body as twoonone.RoomEventResponse
        }
        while (true) {
            const ret = await (async () => new Promise<twoonone.RoomEventResponse|undefined>((done => {
                    if (msg.length > 0) {
                        const me = msg.shift()
                        if (me === undefined) return
                        done(handle(me))
                        return
                    }
                    watch(msg, (mes) => {
                        const me = mes.shift()
                        if (me === undefined) return
                        done(handle(me))
                    }, {once: true})
                }))
            )()
            if (ret === undefined) {
                return
            }
            console.log('ret')
            yield ret
        }
    })()
    // 
    watch(closed, (e) => {
        console.log('websocket ', e)
        sync.return()
    })
    return sync
}

export {
    GetRoom,
    GetRooms,
    GetDaliyCoin,
    CreateRoom,
    JoinRoom,
    ExitRoom,
    RobLandowner,
    NoRobLandowner,
    SendCard,
    NoSendCard,
    StartRoom,
    RoomEvent
}

function statusCode2Error(code: HttpStatusCode):twoonone.api.Error|undefined {
    if (code === HttpStatusCode.Ok) {
        return
    } else if (code === HttpStatusCode.Unauthorized) {
        return twoonone.api.Error.ERROR_NO_AUTH
    }
}

const errorHandle = (e: any) => {
    if (e instanceof axios.AxiosError) {
        const code = statusCode2Error(e.status!)
        if (code !== undefined) return code
    } else if (e instanceof TwoononeError) {
        console.error(e)
        return e.code
    } else {
        console.error(e)
    }
}